:root {
    --white: #ffffff;
    --black: #000000;
    --gray: #808080;
    --red: #c90000;
    --red-clear: #c74e4e;
    --green: #1a9b00;
    --green-clear: #81c773;
}

:export {
    red: var(--red);
    redClear: var(--red-clear);
    green: var(--green);
    greenClear: var(--green-clear);
    white: var(--white);
    black: var(--black);
    gray: var(--gray);
}

.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

}


/* Navigation */
.navigation a {
    padding: .4rem .8rem;
    border-radius: 10rem;
    transition: .2s ease;
}

.navigation a.active {
    padding: .4rem .8rem;
    border-radius: 10rem;
    transition: .2s ease;
    background: var(--white) !important;
    color: #4f46e5 !important;
}


/* loader anim */
.loader-nm,
.loader-nm-gray {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: inline-block;
    border-top: 2px solid #FFF;
    border-right: 2px solid transparent;
    box-sizing: border-box;
    animation: loaderBtn .3s linear infinite;
}

.loader-nm-gray {
    border-top: 2px solid #aaa;
}

.loader-link,
.loader-link-gray,
.loader-link-black {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: inline-block;
    border-top: 2px solid #FFF;
    border-right: 2px solid transparent;
    box-sizing: border-box;
    animation: loaderBtn .3s linear infinite;
}

.loader-link-gray {
    border-top: 2px solid #aaa;
}

.loader-link-black {
    border-top: 2px solid #101010;
}

@keyframes loaderBtn {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.bloc-newsletter-light {
    background-image: url(../src/img/newsletter_bg_light.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;
}

.bloc-newsletter-dark {
    background-image: url(../src/img/newsletter_bg_dark.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;
}

@media (max-width:768px) {

    .bloc-newsletter-light,
    .bloc-newsletter-dark {
        background-image: none;
    }
}